<template>
    <v-card v-if="show_data">
        <v-card-title primary-title>
            <div class="headline">
                {{ component.title.split(":")[0] }}:<span class="font-weight-light">{{ component.title.split(":")[1] }}</span>
            </div>
            <v-spacer/>

            <v-tooltip left>
                <v-btn slot="activator" flat icon @click.stop="to_clipboard">
                    <v-icon color="primary">assignment</v-icon>
                </v-btn>
                <span>Copy Component</span>
            </v-tooltip>
        </v-card-title>
        <v-divider></v-divider>
        <v-layout style="padding: 0.2em 16px;" v-if="component.description">
            {{ component.description }}
        </v-layout>
        <v-divider></v-divider>
        <v-expansion-panel expand>
            <component-structure :structure="component.structure"/>
            <component-problems :problems="component.known_problems"/>   
        </v-expansion-panel>
    </v-card>
    <v-progress-linear :indeterminate="true" v-else></v-progress-linear>
</template>

<script>
import load from "../scripts/load.js";
import ComponentStructure from "../components/component/Structure.vue";
import ComponentProblems from "../components/component/Problems.vue";

export default {
    components: {
        ComponentStructure,
        ComponentProblems
    },
    created() {
        this.helper = (component_info) => {
            if(!this.component_info) {
                window.setTimeout(this.helper, 100);
            } else {
                load(this.component_info.name, this.component_info.link);
            }
        }
        this.helper();
    },
    data() {
        return {
            helper: null
        };
    },
    computed: {
        show_data() {
            return this.component_info != undefined && this.component != undefined;
        },
        component() {
            return this.$store.state[this.component_info.name];
        },
        component_info() {
            let comp = this.$route.params.name;
            return this.all_components.filter(e => e.name == comp)[0];
        },
        all_components() {
            return this.$store.state.components;
        }
    },
    methods: {
        to_clipboard() {
            navigator.permissions.query({name: "clipboard-write"})
            .then(result => {
                if (result.state == "granted" || result.state == "prompt") {
                   let def_obj = { [this.component_info.name]: this.get_default(this.component.structure) };
                    navigator.clipboard.writeText(JSON.stringify(def_obj, null, "\t"));
                }
            });
        },
        get_default(structure, first=true) {
            if(!structure) return;
            if(!first && (structure.structure == undefined || (structure.type != "Object" && structure.type != "Array"))) {
                return structure.default || "-";
            }
            
            let def = {};
            let it = structure;
            if(!first) it = structure.structure;
            console.log(structure.structure)
            
            for(let key in it) {
                console.log(key)
                def[key] = this.get_default(it[key], false);
            }

            return def;
        }
    },
    watch: {
        '$route' (to, from) {
            load(this.component_info.name, this.component_info.link);
        }
    }
}
</script>

